*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.content{
    min-height: 70vh;
}


.filters{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px 20px;
    box-shadow: 5px 5px 10px #ccc;
}

.anticon svg{
    font-size: 20px;
    cursor: pointer;
}

.switch-icons{
    border: 1px solid black;
    border-radius: 5px;
    padding: 10px 20px;
}

.active-icon{
    color: black;
}

.inactive-icon{
    color: gray;
}
